import Endpoint from '@/services/Endpoint'

export default {
  tellerStatuses (payload) {
    return Endpoint().get('/s1/bank/tellerStatus', {
      params: payload
    })
  },
  createTellerStatus (payload) {
    return Endpoint().post('/s1/bank/tellerStatus', payload)
  },
  modifyTellerStatus (payload) {
    return Endpoint().patch('/s1/bank/tellerStatus', payload)
  }
}
