<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="tellerStatusTableData" :noDataText="tellerStatusTableNoDataText" :sst="true" search @search="filterTellerStatusTable" @sort="orderTellerStatusTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadTellerStatusTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="tellerStatusTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ tellerStatusTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="tellerStatusTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="statusId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="statusName">
            {{ this.$t('STATUS_NAME') }}
          </vs-th>
          <vs-th sort-key="externalStatusId">
            {{ this.$t('EXTERNAL_ID') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].statusId">
              {{ data[index].statusId }}
            </vs-td>
            <vs-td :data="data[index].statusName">
              <span :class="disabledClass(data[index])">{{ data[index].statusName }}</span>
            </vs-td>
            <vs-td :data="data[index].externalStatusId">
              {{ data[index].externalStatusId | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="tellerStatusTableTotalPages" v-model="tellerStatusTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import TellerStatusTableMixin from '@/mixins/bank/TellerStatusTableMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'

export default {
  name: 'teller-status-popup',
  mixins: [PopupMixin, TellerStatusTableMixin, DisabledClassMixin],
  mounted: function () {
    this.loadTellerStatusTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
