<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
    <vs-table class="mb-base" :data="tellerTypeTableData" :noDataText="tellerTypeTableNoDataText" :sst="true" search @search="filterTellerTypeTable" @sort="orderTellerTypeTable" @selected="selectRow">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="loadTellerTypeTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="tellerTypeTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ tellerTypeTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="tellerTypeTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="typeId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="typeName">
          {{ this.$t('TYPE_NAME') }}
        </vs-th>
        <vs-th sort-key="externalTypeId">
          {{ this.$t('EXTERNAL_ID') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].typeId">
            {{ data[index].typeId }}
          </vs-td>
          <vs-td :data="data[index].typeName">
            <span :class="disabledClass(data[index])">{{ data[index].typeName }}</span>
          </vs-td>
          <vs-td :data="data[index].externalTypeId">
            {{ data[index].externalTypeId | placeholder('-') }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="tellerTypeTableTotalPages" v-model="tellerTypeTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
</div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import TellerTypeTableMixin from '@/mixins/bank/TellerTypeTableMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'

export default {
  name: 'teller-type-popup',
  mixins: [PopupMixin, TellerTypeTableMixin, DisabledClassMixin],
  mounted: function () {
    this.loadTellerTypeTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
