import Endpoint from '@/services/Endpoint'

export default {
  tellerTypes (payload) {
    return Endpoint().get('/s1/bank/tellerType', {
      params: payload
    })
  },
  createTellerType (payload) {
    return Endpoint().post('/s1/bank/tellerType', payload)
  },
  modifyTellerType (payload) {
    return Endpoint().patch('/s1/bank/tellerType', payload)
  }
}
