import TellerStatusService from '@/services/bank/TellerStatusService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TellerStatusTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultTellerStatusTableFilter: {
      type: String,
      default: null
    },
    defaultTellerStatusTableOrderBy: {
      type: String,
      default: null
    },
    statusBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      tellerStatusTableData: [],
      tellerStatusTableFilterTimer: null,
      tellerStatusTableFilter: this.defaultTellerStatusTableFilter,
      tellerStatusTableOrderBy: this.defaultTellerStatusTableOrderBy,
      tellerStatusTableContainer: 'loading-container',
      tellerStatusTableBusy: false,
      tellerStatusTableTotalRows: 0,
      tellerStatusTableCurrentPage: 1,
      tellerStatusTablePerPage: 10
    }
  },
  computed: {
    tellerStatusTableNoDataText: function () {
      return this.tellerStatusTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    tellerStatusTableTotalPages: function () {
      return Math.ceil(this.tellerStatusTableTotalRows / this.tellerStatusTablePerPage)
    },
    tellerStatusTablePagingInfo: function () {
      return this.pagingInfo(this.tellerStatusTableCurrentPage, this.tellerStatusTablePerPage, this.tellerStatusTableTotalRows)
    }
  },
  methods: {
    loadTellerStatusTableData (loadingContainerId) {
      let self = this
      this.tellerStatusTableBusy = true
      this.tellerStatusTableContainer = loadingContainerId || this.tellerStatusTableContainer
      this.startLoading(this.tellerStatusTableContainer)
      TellerStatusService.tellerStatuses({
        pageIndex: this.tellerStatusTableCurrentPage - 1,
        pageSize: this.tellerStatusTablePerPage,
        filter: this.tellerStatusTableFilter,
        orderByField: this.tellerStatusTableOrderBy,
        statusBehaviourEnumId: this.statusBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.tellerStatusTableContainer)
        self.tellerStatusTableBusy = false
        self.tellerStatusTableTotalRows = response.data.totalRows
        self.tellerStatusTableData = response.data.tellerStatusList || []
      }).catch(function (error) {
        self.stopLoading(self.tellerStatusTableContainer)
        self.tellerStatusTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTellerStatusTable () {
      this.loadTellerStatusTableData()
    },
    filterTellerStatusTable (filter) {
      let self = this
      if (this.tellerStatusTableFilter !== filter) {
        clearTimeout(this.tellerStatusTableFilterTimer)
        this.tellerStatusTableFilterTimer = setTimeout(() => {
          self.tellerStatusTableFilter = filter
          self.loadTellerStatusTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.tellerStatusTableFilterTimer)
      }
    },
    orderTellerStatusTable (orderBy, active) {
      this.tellerStatusTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTellerStatusTableData()
    }
  },
  watch: {
    tellerStatusTableCurrentPage: function () {
      this.refreshTellerStatusTable()
    },
    tellerStatusTablePerPage: function () {
      this.refreshTellerStatusTable()
    },
    statusBehaviourEnumId: function () {
      this.refreshTellerStatusTable()
    }
  }
}

export default TellerStatusTableMixin
