import TellerTypeService from '@/services/bank/TellerTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TellerTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultTellerTypeTableFilter: {
      type: String,
      default: null
    },
    defaultTellerTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      tellerTypeTableData: [],
      tellerTypeTableFilterTimer: null,
      tellerTypeTableFilter: this.defaultTellerTypeTableFilter,
      tellerTypeTableOrderBy: this.defaultTellerTypeTableOrderBy,
      tellerTypeTableContainer: 'loading-container',
      tellerTypeTableBusy: false,
      tellerTypeTableTotalRows: 0,
      tellerTypeTableCurrentPage: 1,
      tellerTypeTablePerPage: 10
    }
  },
  computed: {
    tellerTypeTableNoDataText: function () {
      return this.tellerTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    tellerTypeTableTotalPages: function () {
      return Math.ceil(this.tellerTypeTableTotalRows / this.tellerTypeTablePerPage)
    },
    tellerTypeTablePagingInfo: function () {
      return this.pagingInfo(this.tellerTypeTableCurrentPage, this.tellerTypeTablePerPage, this.tellerTypeTableTotalRows)
    }
  },
  methods: {
    loadTellerTypeTableData (loadingContainerId) {
      let self = this
      this.tellerTypeTableBusy = true
      this.tellerTypeTableContainer = loadingContainerId || this.tellerTypeTableContainer
      this.startLoading(this.tellerTypeTableContainer)
      TellerTypeService.tellerTypes({
        pageIndex: this.tellerTypeTableCurrentPage - 1,
        pageSize: this.tellerTypeTablePerPage,
        filter: this.tellerTypeTableFilter,
        orderByField: this.tellerTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.tellerTypeTableContainer)
        self.tellerTypeTableBusy = false
        self.tellerTypeTableTotalRows = response.data.totalRows
        self.tellerTypeTableData = response.data.tellerTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.tellerTypeTableContainer)
        self.tellerTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTellerTypeTable () {
      this.loadTellerTypeTableData()
    },
    filterTellerTypeTable (filter) {
      let self = this
      if (this.tellerTypeTableFilter !== filter) {
        clearTimeout(this.tellerTypeTableFilterTimer)
        this.tellerTypeTableFilterTimer = setTimeout(() => {
          self.tellerTypeTableFilter = filter
          self.loadTellerTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.tellerTypeTableFilterTimer)
      }
    },
    orderTellerTypeTable (orderBy, active) {
      this.tellerTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTellerTypeTableData()
    }
  },
  watch: {
    tellerTypeTableCurrentPage: function () {
      this.refreshTellerTypeTable()
    },
    tellerTypeTablePerPage: function () {
      this.refreshTellerTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshTellerTypeTable()
    }
  }
}

export default TellerTypeTableMixin
